import React, {useRef} from 'react';
import SockJsClient from 'react-stomp';

function Chatting() {
    const $websocket = useRef (null);

    const handleMsg = msg => {
        console.log (msg);
    };

    const handleClickSendTo = () => {
        console.log("hello");
        $websocket.current.sendMessage ('/sendTo');
    };

    return (
        <div id="main-content">

            <SockJsClient
                url="http://localhost:8080"
                topics={['/topics/sendTo']}
                onMessage={msg => {
                    console.log (msg);
                }}
                ref={$websocket}
            />

            <div className="row">
                <div className="col-md-6">
                    <form className="form-inline">
                        <div className="form-group">
                            <label htmlFor="connect">WebSocket connection:</label>
                            <button id="connect" type="submit" onClick={handleClickSendTo}>Connect</button>
                            <button id="disconnect" type="submit" disabled="disabled">Disconnect</button>
                        </div>
                    </form>
                </div>
            </div>

            <div className="row">
                <div className="col-md-12">
                    <table id="conversation" className="table table-striped">
                        <thead>
                        <tr>
                            <th>Greetings</th>
                        </tr>
                        </thead>
                        <tbody id="greetings">
                        </tbody>
                    </table>
                </div>
            </div>

            <div className="row">
                <div className="col-md-6">
                    <form className="form-inline">
                        <div className="form-group">
                            <input type="text" id="name" className="form-control" placeholder="Your name here..."></input>
                        </div>
                        <button id="send" className="btn btn-default" type="submit">Send</button>
                    </form>
                </div>
            </div>

        </div>
    );
}

export default Chatting;